.gif_card{
      width: 158px;
      height: 210px;
      padding: 2rem;
      background: var(--grys-200);
      display: flex;
      flex-direction: column;
      align-items: center;
      margin-bottom: 3rem;
}

.gif_img{
      width: 110px;
      height: 110px;
      background: var(--light);
      border: solid .1rem var(--grys-300);
      display: flex;
      align-items: center;
      justify-content: center;
      margin: .5rem;
      flex-shrink: 0;
}

.gif_img__box{
      width: 80px;
      height: 80px;
}

@keyframes loop_gif{
      100%{
            background-position: right top;
      }
}

.gif_model{
      width: 100%;
      height: auto;
      margin-top: 2.5rem;
      border-top: solid .1rem var(--grys-300);
}

.gif_model strong{
      padding: 10px;
      display: block;
      text-align: center;
      text-transform: uppercase;
      font-size: 1.4rem;
      font-weight: 600;
}